import * as React from 'react';
import { PrimaryButton } from 'office-ui-fabric-react';
import { Panel } from 'office-ui-fabric-react/lib/Panel';
import { useBoolean } from '@uifabric/react-hooks';
import { TextField } from 'office-ui-fabric-react/lib/TextField';
import { Request } from '../../Request';


export default function UserMenu() {
  const [isOpen, { setTrue: openPanel, setFalse: dismissPanel }] = useBoolean(false);
  const req = new Request();
  const login = () => {
    req.post('account/login',{email: 'fdsfs', password: 'dsfsd'})
    .then((payload) => {
      console.log(payload);
    });
    
  }
    
  if (window.localStorage.getItem('token')) {
    return (
      <div className="loginForm">
        <PrimaryButton onClick={ () => login() } text="" />
      </div>
    );
  } else {
    return (
      <div className="loginForm">
        <PrimaryButton onClick={openPanel} text="" />
        <Panel
          headerText="  "
          isOpen={isOpen}
          onDismiss={dismissPanel}
          closeButtonAriaLabel="Close"
        >
          <a className="btn btn-primary" href="/login/google-oauth2/">
                <img src="/static/images/google.png" />
              </a>

          <div className="ms-Grid" dir="ltr">
            <div className="ms-Grid-row">
              <TextField label="" required />
            </div>
            <div className="ms-Grid-row">
              <TextField label="" required />
            </div>
            <div className="ms-Grid-row">
              <PrimaryButton onClick={ () => login() } text="" />
              <a className="btn btn-primary" href="/login/google-oauth2/">
                  
              </a>

            </div>
          </div>
      </Panel>

      </div>
    )
  }

}